"use client";

import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Progress } from "@/components/ui/progress";
import Image from "next/image";

interface ShipStats {
  attack: number;
  defense: number;
  speed: number;
  energy: number;
}

interface ShipCardProps {
  id: string;
  name: string;
  type: string;
  level: number;
  imageUrl: string;
  stats: ShipStats;
  onUpgrade?: (id: string) => void;
  onSelect?: (id: string) => void;
}

export function ShipCard({
  id,
  name,
  type,
  level,
  imageUrl,
  stats,
  onUpgrade,
  onSelect,
}: ShipCardProps) {
  return (
    <Card className="overflow-hidden bg-gray-900 border-gray-800 transition-all hover:border-blue-500 group">
      <div className="relative aspect-[16/9] w-full">
        <Image
          src={imageUrl}
          alt={name}
          fill
          sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
          className="object-cover transition-transform group-hover:scale-105"
          priority
          onError={(e) => {
            e.currentTarget.src = "https://images.pexels.com/photos/2150/sky-space-dark-galaxy.jpg";
          }}
        />
        <div className="absolute inset-0 bg-gradient-to-t from-gray-900 to-transparent" />
        <div className="absolute top-2 right-2 bg-black/60 backdrop-blur-sm px-3 py-1 rounded-full">
          <span className="text-sm font-medium bg-gradient-to-r from-blue-400 to-purple-400 bg-clip-text text-transparent">
            Level {level}
          </span>
        </div>
      </div>

      <div className="p-6">
        <h3 className="text-2xl font-bold text-white mb-1 bg-gradient-to-r from-blue-400 to-purple-400 bg-clip-text text-transparent">
          {name}
        </h3>
        <p className="text-sm text-gray-400 mb-6">{type}</p>

        <div className="space-y-4 mb-6">
          <StatBar label="Attack" value={stats.attack} color="from-red-500 to-orange-500" />
          <StatBar label="Defense" value={stats.defense} color="from-blue-500 to-cyan-500" />
          <StatBar label="Speed" value={stats.speed} color="from-green-500 to-emerald-500" />
          <StatBar label="Energy" value={stats.energy} color="from-purple-500 to-pink-500" />
        </div>

        <div className="flex gap-3">
          <Button
            variant="outline"
            className="flex-1 border-blue-500 text-blue-500 hover:bg-blue-950 transition-colors"
            onClick={() => onSelect?.(id)}
          >
            Select
          </Button>
          <Button
            className="flex-1 bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 transition-all"
            onClick={() => onUpgrade?.(id)}
          >
            Upgrade
          </Button>
        </div>
      </div>
    </Card>
  );
}

function StatBar({ label, value, color }: { label: string; value: number; color: string }) {
  return (
    <div>
      <div className="flex justify-between text-sm mb-1">
        <span className="text-gray-400">{label}</span>
        <span className={`bg-gradient-to-r ${color} bg-clip-text text-transparent font-medium`}>
          {value}%
        </span>
      </div>
      <div className="h-2 bg-gray-800 rounded-full overflow-hidden">
        <div
          className={`h-full bg-gradient-to-r ${color} transition-all animate-pulse`}
          style={{ width: `${value}%` }}
        />
      </div>
    </div>
  );
}
