"use client";

import { useState } from "react";
import { ShipCard } from "@/components/game/ShipCard";
import { Button } from "@/components/ui/button";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Card } from "@/components/ui/card";

// Mock data for initial ships
const initialShips = [
  {
    id: "1",
    name: "Stellar Vanguard",
    type: "Battleship",
    level: 3,
    imageUrl: "https://images.pexels.com/photos/2150/sky-space-dark-galaxy.jpg",
    stats: {
      attack: 75,
      defense: 85,
      speed: 60,
      energy: 90
    }
  },
  {
    id: "2",
    name: "Nova Scout",
    type: "Scout Ship",
    level: 2,
    imageUrl: "https://images.pexels.com/photos/110854/pexels-photo-110854.jpeg",
    stats: {
      attack: 45,
      defense: 50,
      speed: 95,
      energy: 70
    }
  },
  {
    id: "3",
    name: "Quantum Destroyer",
    type: "Destroyer",
    level: 4,
    imageUrl: "https://images.pexels.com/photos/110854/pexels-photo-110854.jpeg",
    stats: {
      attack: 90,
      defense: 70,
      speed: 65,
      energy: 85
    }
  }
];

export default function FleetPage() {
  const [ships, setShips] = useState(initialShips);
  const [selectedShipId, setSelectedShipId] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [resources] = useState({
    credits: 10000,
    metal: 500,
    crystal: 300,
    energy: 1000
  });

  const handleUpgrade = (shipId: string) => {
    try {
      setShips(ships.map(ship => {
        if (ship.id === shipId) {
          // Simulate upgrade by increasing stats
          return {
            ...ship,
            level: ship.level + 1,
            stats: {
              attack: Math.min(100, ship.stats.attack + 5),
              defense: Math.min(100, ship.stats.defense + 5),
              speed: Math.min(100, ship.stats.speed + 5),
              energy: Math.min(100, ship.stats.energy + 5)
            }
          };
        }
        return ship;
      }));
    } catch (err) {
      setError("Failed to upgrade ship. Please try again.");
    }
  };

  const handleSelect = (shipId: string) => {
    setSelectedShipId(shipId);
  };

  const selectedShip = ships.find(s => s.id === selectedShipId);

  return (
    <div className="min-h-screen bg-black text-white">
      <div className="max-w-7xl mx-auto px-4 py-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-4xl font-bold mb-4 bg-gradient-to-r from-blue-500 to-purple-600 bg-clip-text text-transparent">
            Fleet Management
          </h1>
          <p className="text-gray-400 mb-6">
            Manage and upgrade your fleet of ships to dominate the galaxy
          </p>
          
          {error && (
            <Alert variant="destructive" className="mb-6">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}
        </div>

        {/* Resources Bar */}
        <Card className="p-4 bg-gray-900/50 backdrop-blur-sm border-gray-800 mb-8">
          <div className="grid grid-cols-4 gap-4">
            <div>
              <p className="text-sm text-gray-400">Credits</p>
              <p className="text-xl font-bold text-yellow-400">{resources.credits.toLocaleString()}</p>
            </div>
            <div>
              <p className="text-sm text-gray-400">Metal</p>
              <p className="text-xl font-bold text-gray-300">{resources.metal.toLocaleString()}</p>
            </div>
            <div>
              <p className="text-sm text-gray-400">Crystal</p>
              <p className="text-xl font-bold text-blue-400">{resources.crystal.toLocaleString()}</p>
            </div>
            <div>
              <p className="text-sm text-gray-400">Energy</p>
              <p className="text-xl font-bold text-purple-400">{resources.energy.toLocaleString()}</p>
            </div>
          </div>
        </Card>

        {/* Selected Ship Details */}
        {selectedShip && (
          <Card className="p-6 bg-gray-900/50 backdrop-blur-sm border-gray-800 mb-8">
            <div className="flex items-center justify-between">
              <div>
                <h2 className="text-2xl font-bold text-blue-400 mb-2">
                  {selectedShip.name}
                </h2>
                <p className="text-gray-400">
                  Level {selectedShip.level} {selectedShip.type}
                </p>
              </div>
              <Button 
                variant="outline"
                onClick={() => setSelectedShipId(null)}
                className="border-blue-500 text-blue-500 hover:bg-blue-950"
              >
                Clear Selection
              </Button>
            </div>
          </Card>
        )}

        {/* Fleet Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {ships.map((ship) => (
            <ShipCard
              key={ship.id}
              {...ship}
              onUpgrade={handleUpgrade}
              onSelect={handleSelect}
            />
          ))}
        </div>

        {/* Add New Ship Button */}
        <div className="mt-8 text-center">
          <Button
            size="lg"
            className="bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700"
          >
            Add New Ship
          </Button>
        </div>
      </div>
    </div>
  );
}
